import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useKeyValue } from '../components/spreadsheet/useSheet';
import { loadData } from './accountSlice';
import config from '../../config';

const useAccount = () => {
    const dispatch = useDispatch();

    const {
        loading: infoLoading,
        error: infoError,
        data: infoData,
    } = useKeyValue({
        spreadsheetId: config.spreadsheets.account.id,
        sheetName: "info",
    });

    useEffect(() => {
        if (Array.isArray(infoData) && infoData.length > 0 && !infoLoading && !infoError) {
            console.log(`Dispatching infoData:`, infoData);
            dispatch(loadData(infoData));
        }
    }, [infoData, infoLoading, infoError, dispatch]);

    return {
        loading: infoLoading,
        error: infoError,
        data: infoData,
    };
};

export { useAccount };
